import React from "react";
import styled from "styled-components";

const RateTingStyles = styled.div`
  .rating span {
    color: #ffc000;
  }
`;
const Rating = ({ rating, numReviews, className = "", caption }) => {
  return (
    <RateTingStyles>
      <div className={`rating ${className}`}>
        <span>
          <i
            className={
              rating >= 1
                ? "fas fa-star"
                : rating >= 0.5
                ? "fas fa-star-half-alt"
                : "far fa-star"
            }
          />
        </span>
        <span>
          <i
            className={
              rating >= 2
                ? "fas fa-star"
                : rating >= 1.5
                ? "fas fa-star-half-alt"
                : "far fa-star"
            }
          />
        </span>
        <span>
          <i
            className={
              rating >= 3
                ? "fas fa-star"
                : rating >= 2.5
                ? "fas fa-star-half-alt"
                : "far fa-star"
            }
          />
        </span>
        <span>
          <i
            className={
              rating >= 4
                ? "fas fa-star"
                : rating >= 3.5
                ? "fas fa-star-half-alt"
                : "far fa-star"
            }
          />
        </span>
        <span>
          <i
            className={
              rating >= 5
                ? "fas fa-star"
                : rating >= 4.5
                ? "fas fa-star-half-alt"
                : "far fa-star"
            }
          />
        </span>
        {caption ? (
          <span>{caption}</span>
        ) : (
          <span>{" " + numReviews + " reviews"}</span>
        )}
      </div>
    </RateTingStyles>
  );
};

export default Rating;
